.form-programm {
    margin-bottom: 82px;
}

.form-row {
    display: flex;
    align-items: flex-start;
    margin-bottom: 69px;
    flex-wrap: wrap;
}

.input-box {
    display: flex;
    flex-direction: column;
    width: calc(100% - 648px);
}

.radio-box {
    width: 100%;
    max-width: 568px;
    border: 1px solid #E7E7E7;
    padding: 24px 28px;
    margin-left: 80px;
    label {
        margin-bottom: 13px;
        font-family: $font-secondary;
        font-size: 20px;
        line-height: 30px;
        text-transform: uppercase;
        color: #222;
    }

    >div:last-child {
        label {
            margin-bottom: 0;
        }
    }
}

.input-row {
    margin-bottom: 20px;
    display: flex;
    align-items: center;
    width: 100%;
    &:last-child {
        margin-bottom: 0;
    }

    label {
        font-weight: 400;
        font-size: 20;
        line-height: 30px;
        text-transform: uppercase;
        color: #444;
        font-family: $font-secondary;
        margin-right: 14px;
        white-space: nowrap;
    }
    input, textarea {
        border: 1px solid #E7E7E7;
        padding: 10px 19px;
        font-size: 20px;
        line-height: 1,5;
        font-family: $font-secondary;
        width: 100%;
        &:focus-visible {
            outline: none;
        }

    }
    textarea {
        height: 174px;
    }
}

.form-row-title {
    width: 100%;
    margin-bottom: 75px;
    // margin-top: -75px;
    margin-left: -40px;
    span {
        position: relative;
        padding: 0 40px;
        background-color: white;
        &::before {
            content: "";
            position: absolute;
            left: -100vw;
            width: 200vw;
            top: 27px;
            z-index: -1;
            background-color: $primary-color;
            height: 4px;
        }
        
    }
    
    
}

.data{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    position: relative; 
}

.email{
    display: flex;
    width: 100%;
    position: relative;
    margin-right: 78px;
    :before{
    content: "";
    background: url(assets/img/icon_email.svg) no-repeat center;
    position: absolute;
    top: 14px;
    right: 21px;
    width: 20px;
    height: 20px;
    }
    #mail{
      height: 52px;
    }
  }
  
  .call{
    display: flex;
    width: 100%;
    max-width: 892px;
    position: relative;
    ::before{
      content:"";
      background: url(assets/img/icon-phone.svg) no-repeat;
      position: absolute;
      top: 17px;
      right: 13px;
      width: 20px;
      height: 20px;
      z-index: 1;
  }
  #phone{
    width: 100%;
    max-width: 750px;
    font-size: 16px;
    height: 52px;
    margin-left: 6px;
  }
  }


