// }

.check-row {
    
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.input-item {
    display: flex;
    align-items: center;
    input.btn.btn-primary {
      width: 568px;
      margin-top: 61px;
      height: 52px;
    }
       .special-description {
        margin-left: 80px;
        margin-top: 59px;
    }
}

