.example {

    background-color: $secondary-color;
    padding-bottom: $section-margin;    
    &-row {
        display: flex;
        justify-content: space-between;
        margin: 0 -12px;
    }
    &-title {
        @extend .title-1;
        margin-bottom: 70px;
        padding-bottom: 76px;
        background: #fff;
        position: relative;
        z-index: 1;
        &:before {
            content: "";
            position: absolute;
            left: -100vw;
            top: 0;
            width: 200vw;
            height: 100%;
            background-color: $white;
            z-index: -1;
            
        }

    }
    &-options {
        max-width: 436px;
        &-description {
            margin-bottom: 69px;
        }
        &-params {
            display: flex;
            justify-content: space-between;
            font-size: 30px;
            line-height: 37px;
            max-width: 370px;
            margin-bottom: 60px;

            &-item {
                display: flex;
                flex-direction: column;
                text-align: center;
                border: 1px solid #CDCDCD;
                border-radius: 3px;
                padding-top: 15px;
                min-width: 162px;
                

                span {
                    font-size: 14px;
                    display: inherit;
                    margin: 0 auto -18px;
                    padding: 0 5px;
                    background-color: $secondary-color;                    
                }
            }
        }

        &-total {
            @extend .title-4;
            text-transform: uppercase;
            display: flex;
            justify-content: space-between;
            
            max-width: 327px;

        }
        
    }



    &-slider {
        ::selection {
            background: none;
        }
        position: relative;
        width: 420px;
        height: 269px;
        display: flex;
        margin-top: 9px;
        // margin-right: 35px;
        &-wrapper{
            width: 420px;
            height: 269px;
            margin-left: auto;
            position: absolute;
            left: 50%;
            top: 30%;
            transform:translate3d(-50%, -50%, 0);
            z-index: 1;
        } 
        &-description {
            @extend .title-4;
            line-height: 30px;
            font-family: $font-secondary;
            text-transform: uppercase;
            display: flex;
            width: 100%;
            justify-content: space-between;
            margin: auto auto -40px;
            position: relative;
            z-index: 0;

           
            &::before{
                content: "";
                width: 100%;
                position: absolute;
                left: 0;
                top: 8px;
                height: 6px;
                border-radius: 6px;
                background: #DCDCDC;
                // margin-top: 60px;
            }
            & div:first-child {
                margin-left: -70px;

                
            }
            & div:last-child {
                margin-right: -70px;
            }
        }
    }

       
    //   image
    .before,
    .after {
        width:100%;
        height:100%;
        background: center/cover no-repeat #F2F2F2;;
        position: absolute;
        top:0;
        left:0;
        pointer-events:none;
        overflow: hidden;
        display: flex;
        align-items: center;
    }
      
    .content-image{
        max-width: 420px;
    }
      

      
    .scroller{
        width: 35px;
        height: 35px;
        position: absolute;
        left: 100px;
        top: calc(100% + 85px);
        transform:translateY(-50%);
        border-radius:50%;
        background-color: transparent;
        opacity:1;
        pointer-events:auto;
        cursor: pointer;
        
        
        &:hover{
            opacity:1;
        }
        
        &__thumb{
            width:100%;
            height:100%;
        }
    }
        


}






