///fonts///
$font-primary: "Arial";
$font-secondary:  "Oswald";
$font-size: 16px;
$section-title: 32px;

///colors///
$white: #fff;
$black: #000;

$primary-color: #68B738;
$secondary-color: #F2F2F2;
$primary-color-hover: #5EAA2F;

/*grey*/
$label-red: #F93838;
$label-purpur: #8683EF;
$label-green: #70D61F;

$required: $primary-color;
$placeholder: #bdbdbd;

/*text-default*/
$font-size: 16px;
$line-height: 1.4;
$text: #000;

/*   <Breadcrumbs/>   */
$breadcrumbs-slash: $secondary-color;
$breadcrumbs-link: $secondary-color;
$breadcrumbs-link-hover: $primary-color;
$breadcrumbs-last-child: #B2B1B1;

/*   <Muve-top/>   */
$muve-top-background: $primary-color;
$muve-top-color: #fff;

/*   margin   */

$section-margin: 72px;
$section-mobile-margin: 36px;