@media screen and (max-width: 991px) {
    header {


        
        .navbar {
            > .container-fluid 
            {align-items: center;}
            padding: 12px 8px 17px;
            

            .navbar-nav {
                padding: 36 15 12px;
                .nav-link {
                    color: #000 !important;
                    &:hover::before {
                        display: none;
                    }
                }
            }
        }
    .navbar-toggler {
        position: relative;
        &[aria-expanded="true"] {
            span {
                transform: rotate(45deg) translate(8px, 8px);
                &:nth-child(2) {                    
                    transform: rotate(-45deg) translate(0px, 0px);
                }                
                &:nth-child(3) {
                    top: 1px;
                    transform: rotate(-45deg) translate(0px, 0px);
                    opacity: 0;
                }
            }            
            }
        }
        span {
            background-color: #000;
            height: 2px;
            width: 100%;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
            transition: .4s;
            &:nth-child(2) {
                top: 50%;
            }
            &:nth-child(3) {
                top: 100%;
            }
        }
        
    }
    .navbar-brand {
        padding: 0;
        img {
            height: 37px;
        }
        
    }    
    .btn {
        padding: 10px 26px;
        font-size: 14px;
        line-height: 18px;
    }
    
    .first-screen {
        margin-top: 0;
        padding-top: 27px;

        margin-bottom: calc(135px + 40px);
        background: none;        
        &::after {
            content: "";
