.map{
    position: relative;
    width: 100%;
    height: 400px;

    &-info{
        display: flex;
        justify-content: space-around;
        align-items: center;
        background-color: $white;
        width: 100%;
        max-width: 570px;
        height: 200px;
        position: absolute;
        top: 95px;
        left: 350px;

        .title-4 {            
            font-size: 20px;            
            font-family: $font-secondary;
            width: 163px;
            text-transform: uppercase;
        }

        address {            
            // margin-top: 20px;
            font-size: 16px;            
            font-family: $font-primary;
            color: $black; 
            margin-bottom: 0;
            max-width: 104px;         
            }
        .street {
            margin-bottom: 20px;
        }  
    }

    iframe{
        width: 100%;
        height: 100%;
    }
}