footer {
  padding: 60px;
  background-color: rgba(242, 242, 242, 1);
  margin-top: auto;
  align-items: center;
  height: 149px;
  .container {
    display: flex;
    justify-content: space-between;
    align-items: center; 
  
    &-social-networks {
      padding-left: 24px;
    }
  
    ul.footer-social-networks {
      display: flex;
      margin-top: 14px;
      align-items: center;
    }
  
    .footer-social-networks-item li::before {
      display: none;
    }
  }
  div {
    font-size:16px;
    line-height: 18px;
    align-items: center;
    display: flex;    
  }
  
  .footer-academy img {
    padding-left: 19px;
  }

}