.first-screen {
    padding-top: 223px;
    margin-top: -111px;
    background: url(assets/img/first-screen.png) calc(50% + 114px) 163px no-repeat;
    position: relative;
    margin-bottom: $section-margin;
    &::before {
        content: "";
        position: absolute;
        width: 50vw;
        height: 100%;
        background: url(assets/img/first-screen-background.png) no-repeat;
        top: 0;
        right: 0;
        z-index: -1;
    }
    >.container {
        padding: 0 80px;        
        min-height: 470px;
    }

    &-title {
        max-width: 414px;
        margin-bottom: 40px;
    }
    &-subtitle {
        @extend .title-4;
        margin-bottom: 52px;
    }
}

