.products-row{
    display: flex;
	justify-content: space-between;
	margin-bottom: 86px;
}

.table-box {
	overflow: auto;
	
}

.category-table{
	width: 100%;
	max-width: 898px;
	min-width: 767px;
	border-collapse:collapse;
	border-spacing:0;
	height: 288px;
	.btn {
		width: 245px;
		height: 40px;
		margin-right: -76px;
		padding: 0px;
	}
}
.category-table,
.category-table td,
.category-table th {
	border: none;
	border-bottom: 1px solid #CDCDCD;
}
.category-table td,
.category-table th {
	padding: 3px;
	// width: 30px;
	height: 35px;
}
.category-table th {
	background: #347c99; 
	color: #fff; 
	font-weight: normal;
}

.category-table tr {
	&:first-child {
		td {
			border-top: 1px solid #CDCDCD;
		}
	}
}

.product-category {
	font-family: $font-secondary;
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 20px;

	text-transform: uppercase;
}


.box-gift {
	background: url(assets/img/table-image.png) no-repeat;

    width: 245px;
    height: 288px;
    // margin-left: 80px;
	
}



.gift-description {
	position: relative;
	width: 161px;
	height: 62px;
	text-align: center;
	display: flex;
	align-items: center;
	font-size: 16px;
	line-height: 20px;
	color: #FFFFFF;
	top: 184px;
	left: 42px;
}

