.how-it-works {
    margin-bottom: $section-margin;
    &-title {
        @extend .title-1;
        margin-bottom: 72px;
    }
    &-item {
        max-width: 245px;
        margin-bottom: 35px;
        &-box {
            width: 80px;
            min-width: 80px;
            height: 80px;           
            margin-bottom: 31px;            
            background: center no-repeat $primary-color;
        }
        &-description {
            color: #444444; 
        }
    }
    ol {
        li:before {
            content: counter(li, decimal) "";
            color: $secondary-color;
            font-size: 280px;
            line-height: 1;
            font-family: $font-secondary;
            top: -35px;
            left: initial;
            right: 50px;
            font-weight: 400;
            z-index: -1;
        }

    }
}

 

